.start-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: transparent;
}
.start-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Koulen', cursive;
    font-style: normal;
    font-weight: 700;
    font-size: 31.25px;
    line-height: 37px;
    text-align: center;
    max-width: 200px;
    padding-bottom: 500px;
}

.start-btn {
    width: 193px;
    height: 52px;
    background-color: #4d5b9e;
    border-radius: 15px;
    color: white;
    font-family: 'Mukta', sans-serif;
    font-weight: 700;
    font-size: 30px;
}

.title {
    font-size: 100px;
}

.cloud {
    position: absolute;
    cursor: pointer;
}
.slow-cloud {
    animation: movecloudsL 10s linear infinite;
    transform: scale(0.5);
}

.medium-cloud {
    animation-name: movecloudsL;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    transform: scale(0.5);
}

.fast-cloud {
    animation-name: movecloudsL;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    transform: scale(0.5);
}
.cloud-text {
    position: absolute;
    left: 200px;
    top: 100px;
    font-size: 100px;
}
@keyframes movecloudsL {
    0% {
        right: -100vmin;
        opacity: 0;
    }
    50% {
        opacity: 100%;
    }
    100% {
        right: 200vmin;
        opacity: 0;
    }
}
@keyframes movecloudsR {
    from {
        left: -1000px;
    }
    to {
        left: 2000px;
    }
}
