.answer {
}
.answers-container {
    display: flex;
    justify-content: center;
    align-content: center;
    font-size: 2.4vmin;
}

.answer-btn {
    border-radius: 5px;
    cursor: auto;
    font-size: 2.4vmin;
}

.unanswered {
    background-color: white;
}
.selected {
    background-color: #d6dbf5;
}

.correctAnswer {
    border: none;
    background-color: #94d7a2;
}

.answeredIncorrectly {
    opacity: 0.5;
    background-color: #f8bcbc;
}
